nav {
    background-color: rgb(125, 141, 191);
  }

nav ul a {
    font-family: 'Anton', sans-serif;
    font-style: italic;
    margin-right: 45px;
    font-size: 2.5rem;
    -webkit-text-stroke-color: black;
    -webkit-text-stroke-width: 1.50px; 
}

nav .brand-logo {
    font-family: 'Anton', sans-serif;
    font-style: italic;
    font-size: 3rem;
    -webkit-text-stroke-color: black;
    -webkit-text-stroke-width: 2.00px; 
}

img.responsive-img, video.responsive-video {
    justify-content: left;
    max-width: 40%;
    margin-left: 10px;
    padding-left: 20px;
}

.red {
    background-color: rgb(51, 51, 153) !important;
  }
.card {
    width:295px;
    display: block;
    /* margin: 60px auto; */
    float: left;
    box-shadow: 10px 5px 40px 20px 
    darken(#341cac, 5%);
    -webkit-transition: .25s;
    transition: .25s;
    margin-left: 50px;
}

.card-content {
    text-align: center;
}

.card-action {
    text-align: center;
}

